import {
  Backdrop,
  ClouseBtn,
  ModalWindow,
  Container,
  Title,
  TitleForm,
  ListForm,
  ItemForm,
  RadioInput,
  RadioLabel,
  FormCakes,
  TasteSelect,
  DesignCakesForm,
  DecorLabel,
  DecorCheckbox,
  CommentCakes,
  FileUploadForm,
  FileInput,
  CancelButton,
  ConfirmButton,
  ButtonsWrapper,
  FileLabel,
  TextModalCustom,
} from "./modal.styled";

import { fillings } from "../../data/data.js";
import { useEffect, useState } from "react";

export const Modal = ({ clouseModal, open }) => {
  const handleClouse = () => {
    clouseModal();
  };

  const [form, setForm] = useState(
    JSON.parse(localStorage.getItem("form")) || ""
  );
  const [size, setSize] = useState(
    JSON.parse(localStorage.getItem("size")) || ""
  );
  const [taste, setTaste] = useState(
    JSON.parse(localStorage.getItem("taste")) || ""
  );
  const [design, setDesign] = useState(
    JSON.parse(localStorage.getItem("design")) || []
  );
  const [comment, setComment] = useState(
    JSON.parse(localStorage.getItem("comment")) || ""
  );
  const [file, setFile] = useState("");

  const handleFormChange = (e) => {
    setForm(e.target.value);
  };
  const handleSizeChange = (e) => {
    setSize(e.target.value);
  };

  const handleTasteChange = (e) => {
    setTaste(e.target.value);
  };

  const handleDesignChange = (e) => {
    setDesign((prevState) =>
      prevState.includes(e.target.value)
        ? prevState.filter((item) => item !== e.target.value)
        : [...prevState, e.target.value]
    );
  };

  const handleCommentChange = (e) => {
    setComment(e.target.value);
  };

  const handleFileChange = (e) => {
    setFile(e.target.value);
  };

  useEffect(() => {
    localStorage.setItem("design", JSON.stringify(design));
    localStorage.setItem("form", JSON.stringify(form));
    localStorage.setItem("size", JSON.stringify(size));
    localStorage.setItem("taste", JSON.stringify(taste));
    localStorage.setItem("comment", JSON.stringify(comment));
    localStorage.setItem("file", JSON.stringify(file));
  }, [design, form, size, taste, comment, file]);

  return (
    <Backdrop open={open}>
      <ModalWindow open={open}>
        <ClouseBtn onClick={handleClouse}>X</ClouseBtn>
        <Container>
          <Title>  </Title>
          <ListForm>
            <ItemForm>
              <TitleForm></TitleForm>
              <FormCakes>
                <RadioInput
                  type="radio"
                  name="form-cakes"
                  id="form-cakes-round"
                  value=""
                  checked={form === ""}
                  onChange={handleFormChange}
                />
                <RadioLabel htmlFor="form-cakes-round"></RadioLabel>

                <RadioInput
                  type="radio"
                  name="form-cakes"
                  id="form-cakes-square"
                  value=""
                  checked={form === ""}
                  onChange={handleFormChange}
                />
                <RadioLabel htmlFor="form-cakes-square"></RadioLabel>

                <RadioInput
                  type="radio"
                  name="form-cakes"
                  id="form-cakes-heart"
                  value=""
                  checked={form === ""}
                  onChange={handleFormChange}
                />
                <RadioLabel htmlFor="form-cakes-heart"></RadioLabel>

                <RadioInput
                  type="radio"
                  name="form-cakes"
                  id="form-cakes-rectangular"
                  value=""
                  checked={form === ""}
                  onChange={handleFormChange}
                />
                <RadioLabel htmlFor="form-cakes-rectangular">
                  
                </RadioLabel>
              </FormCakes>
            </ItemForm>
            <ItemForm>
              <TitleForm>   </TitleForm>
              <FormCakes>
                <RadioLabel htmlFor="size-cakes-one">
                  16   1,5 
                </RadioLabel>
                <RadioInput
                  type="radio"
                  name="size-cakes"
                  id="size-cakes-one"
                  value="16   1,5 "
                  checked={size === "16   1,5 "}
                  onChange={handleSizeChange}
                />

                <RadioInput
                  type="radio"
                  name="size-cakes"
                  id="size-cakes-two"
                  value="18   2 "
                  checked={size === "18   2 "}
                  onChange={handleSizeChange}
                />
                <RadioLabel htmlFor="size-cakes-two">18   2 </RadioLabel>

                <RadioInput
                  type="radio"
                  name="size-cakes"
                  id="size-cakes-three"
                  value="20   2,5-3 "
                  checked={size === "20   2,5-3 "}
                  onChange={handleSizeChange}
                />
                <RadioLabel htmlFor="size-cakes-three">
                  20   2,5-3 
                </RadioLabel>

                <RadioInput
                  type="radio"
                  name="size-cakes"
                  id="size-cakes-four"
                  value="22   4-4,5 "
                  checked={size === "22   4-4,5 "}
                  onChange={handleSizeChange}
                />
                <RadioLabel htmlFor="size-cakes-four">
                  22   4-4,5 
                </RadioLabel>

                <RadioInput
                  type="radio"
                  name="size-cakes"
                  id="size-cakes-five"
                  value="24   5-5,5 "
                  checked={size === "24   5-5,5 "}
                  onChange={handleSizeChange}
                />
                <RadioLabel htmlFor="size-cakes-five">
                  24   5-5,5 
                </RadioLabel>
              </FormCakes>
            </ItemForm>
            <ItemForm>
              <TitleForm></TitleForm>
              <form>
                <TasteSelect
                  id="taste"
                  name="taste"
                  value={taste}
                  onChange={handleTasteChange}
                >
                  {fillings.map(({ id, title }) => (
                    <option value={title} key={id}>
                      {title}
                    </option>
                  ))}
                </TasteSelect>
              </form>
            </ItemForm>
            <ItemForm>
              <TitleForm></TitleForm>
              <DesignCakesForm>
                <DecorLabel>
                  <DecorCheckbox
                    type="checkbox"
                    name="design"
                    value="berries"
                    checked={design.includes("berries")}
                    onChange={handleDesignChange}
                  />
                  
                </DecorLabel>
                <DecorLabel>
                  <DecorCheckbox
                    type="checkbox"
                    name="design"
                    value="Flowers"
                    checked={design.includes("Flowers")}
                    onChange={handleDesignChange}
                  />
                  
                </DecorLabel>
                <DecorLabel>
                  <DecorCheckbox
                    type="checkbox"
                    name="design"
                    value="Chocolate drip"
                    checked={design.includes("Chocolate drip")}
                    onChange={handleDesignChange}
                  />
                   
                </DecorLabel>
                <DecorLabel>
                  <DecorCheckbox
                    type="checkbox"
                    name="design"
                    value="macarons"
                    checked={design.includes("macarons")}
                    onChange={handleDesignChange}
                  />
                  
                </DecorLabel>
                <DecorLabel>
                  <DecorCheckbox
                    type="checkbox"
                    name="design"
                    value="Meringues"
                    checked={design.includes("Meringues")}
                    onChange={handleDesignChange}
                  />
                  
                </DecorLabel>
                <DecorLabel>
                  <DecorCheckbox
                    type="checkbox"
                    name="design"
                    value="Mirror glaze"
                    checked={design.includes("Mirror glaze")}
                    onChange={handleDesignChange}
                  />
                   
                </DecorLabel>
                <DecorLabel>
                  <DecorCheckbox
                    type="checkbox"
                    name="design"
                    value="Minimalizm"
                    checked={design.includes("Minimalizm")}
                    onChange={handleDesignChange}
                  />
                  
                </DecorLabel>
              </DesignCakesForm>
            </ItemForm>
            <ItemForm>
              <TitleForm>    </TitleForm>
              <form>
                <CommentCakes
                  placeholder="',   "
                  value={comment}
                  onChange={handleCommentChange}
                />
              </form>
            </ItemForm>
            <ItemForm>
              <TitleForm></TitleForm>
              <FileUploadForm>
                <FileLabel htmlFor="img-cakes"> </FileLabel>
                <FileInput
                  id="img-cakes"
                  type="file"
                  value={file}
                  onChange={handleFileChange}
                />
              </FileUploadForm>
            </ItemForm>
          </ListForm>
          <TextModalCustom>
              ,     ', 
               !
          </TextModalCustom>
          <ButtonsWrapper>
            <ConfirmButton>  </ConfirmButton>
            <CancelButton onClick={handleClouse}></CancelButton>
          </ButtonsWrapper>
        </Container>
      </ModalWindow>
    </Backdrop>
  );
};
