import {
  DesignSection,
  Container,
  DesignTitle,
  DesignSubTitle,
  DesignTitleCard,
  DesignTextCard,
  DesignList,
  Designbtn,
  DesignListItem,
  CreateCustombtn,
} from "./customDesign.styled";
import { FormIcon } from "../../icons/formIcon";
import { DownloadIcon } from "../../icons/downloadIcon";
import { SizeCakeIcon } from "../../icons/sizeCakeIcon";
import { TasteIcon } from "../../icons/tasteIcon";
import { DecorIcon } from "../../icons/decorIcon";
import { PencilIcon } from "../../icons/pencilIcon";
import { useState, useEffect } from "react";
import { Modal } from "../modal/modal";

export const CustomDesign = () => {
  const [isOpen, setIsOpen] = useState(false);

  const handleToggleHover = () => {
    setIsOpen(!isOpen);
  };

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }

    return () => (document.body.style.overflow = "auto");
  }, [isOpen]);

  return (
    <DesignSection>
      <Container>
        <div>
          <DesignTitle
            initial={{ opacity: 0, y: 40 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            viewport={{ once: true, amount: 0.2 }}
          >
             
          </DesignTitle>
          <DesignSubTitle
            initial={{ opacity: 0, y: 40 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            viewport={{ once: true, amount: 0.2 }}
          >
                -  , ,    
          </DesignSubTitle>
        </div>
        <DesignList
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          viewport={{ once: true, amount: 0.2 }}
        >
          <DesignListItem>
            <Designbtn onClick={handleToggleHover}>
              <DesignTitleCard></DesignTitleCard>
              <FormIcon />
              <DesignTextCard>
                , , , , 
              </DesignTextCard>
            </Designbtn>
          </DesignListItem>
          <DesignListItem>
            <Designbtn onClick={handleToggleHover}>
              <DesignTitleCard></DesignTitleCard>
              <SizeCakeIcon />
              <DesignTextCard>
                 6, 10, 20, 30, 50   
              </DesignTextCard>
            </Designbtn>
          </DesignListItem>
          <DesignListItem>
            <Designbtn onClick={handleToggleHover}>
              <DesignTitleCard></DesignTitleCard>
              <TasteIcon />
              <DesignTextCard>
                , ,   ..
              </DesignTextCard>
            </Designbtn>
          </DesignListItem>
          <DesignListItem>
            <Designbtn onClick={handleToggleHover}>
              <DesignTitleCard></DesignTitleCard>
              <DecorIcon />
              <DesignTextCard>
                , , ,   ..
              </DesignTextCard>
            </Designbtn>
          </DesignListItem>
          <DesignListItem>
            <Designbtn onClick={handleToggleHover}>
              <DesignTitleCard></DesignTitleCard>
              <PencilIcon />
              <DesignTextCard>', ,   ..</DesignTextCard>
            </Designbtn>
          </DesignListItem>
          <DesignListItem>
            <Designbtn onClick={handleToggleHover}>
              <DesignTitleCard></DesignTitleCard>
              <DownloadIcon />
              <DesignTextCard>
                 ,     .
              </DesignTextCard>
            </Designbtn>
          </DesignListItem>
        </DesignList>
        <CreateCustombtn onClick={handleToggleHover}>
            
        </CreateCustombtn>
      </Container>
      <Modal open={isOpen} clouseModal={handleToggleHover} />
    </DesignSection>
  );
};
