import {
  Container,
  HeroTitle,
  HeroSubTitle,
  HeroBtn,
  HeroImage,
  HeroSection,
  HeroWrapInfo,
} from "./hero.styled";
import imgHero from "../../img/heroImage.png";
export const Hero = () => {
  return (
    <HeroSection>
      <Container>
        <HeroWrapInfo>
          <HeroTitle>  - </HeroTitle>
          <HeroSubTitle>
            ,         
          </HeroSubTitle>
          <HeroBtn to="/menu"> </HeroBtn>
        </HeroWrapInfo>
        <HeroImage src={imgHero} alt="" />
      </Container>
    </HeroSection>
  );
};
