import {
  Footer,
  WrapLinkSoc,
  LinkSoc,
  WrapSocList,
  Container,
  FooterLogo,
  FooterNavList,
  NavLink,
  FooterNavTitle,
  WrapText,
  Phone,
  SocLinkFooter,
  SocLinkList,
  Copyright,
} from "./footer.styled";
import { CallIcon } from "../../icons/callIcon";
import { TelegramIcon } from "../../icons/telegramIcon";
import { ViberIcon } from "../../icons/viberIcon";
import { FaceBookIcon } from "../../icons/facebookIcon";
import { InstagramIcon } from "../../icons/instagramIcon";
export const FooterCake = () => {
  return (
    <Footer>
      <Container>
        <WrapText>
          <FooterLogo href="/"></FooterLogo>
          <FooterNavTitle></FooterNavTitle>
          <FooterNavList>
            <li>
              <NavLink to="/"></NavLink>
            </li>
            <li>
              <NavLink to="/menu"></NavLink>
            </li>
            <li>
              <NavLink to="/about"> </NavLink>
            </li>
          </FooterNavList>
        </WrapText>
        <WrapText>
          <FooterNavTitle>'  </FooterNavTitle>
          <SocLinkList>
            <li>
              <Phone href="/">
                <CallIcon />
                +38 (xxx)-xx-xx
              </Phone>
            </li>
            <li>
              <SocLinkFooter href="/" variant="telegram">
                <TelegramIcon />
                Telegram
              </SocLinkFooter>
            </li>
            <li>
              <SocLinkFooter href="/" variant="viber">
                <ViberIcon />
                Viber
              </SocLinkFooter>
            </li>
          </SocLinkList>
          <Copyright>(c) , 2025</Copyright>
        </WrapText>
        <WrapSocList>
          <FooterNavTitle>  </FooterNavTitle>
          <WrapLinkSoc>
            <LinkSoc>
              <InstagramIcon />
            </LinkSoc>
            <LinkSoc>
              <FaceBookIcon />
            </LinkSoc>
          </WrapLinkSoc>
        </WrapSocList>
      </Container>
    </Footer>
  );
};
