import { Link } from "react-router-dom";
import CakeImg from "../../img/cake.jpg";
import CapCakeImg from "../../img/capcake.jpg";
import MakaronImg from "../../img/makaron.jpg";
import TartImg from "../../img/tart.jpg";
import {
  MenuSection,
  Container,
  MenuTitle,
  MenuList,
  MenuCard,
  MenuCardImage,
  MenuName,
  MenuBtn,
  WrapInfo,
} from "./menu.styled";
import { useDispatch } from "react-redux";
import { categoryProducts } from "../../redux/filtersSlice";
import { productsCategory } from "../../redux/constans";

export const Menu = () => {
  const dispatch = useDispatch();
  const handleCategoryCakes = type => dispatch(categoryProducts(type))
  return (
    <MenuSection>
      <Container>
        <MenuTitle
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          viewport={{ once: true, amount: 0.2 }}
        >
          
        </MenuTitle>
        <MenuList>
          <MenuCard
            initial={{ opacity: 0, y: 18, scale: 0.98 }}
            whileInView={{ opacity: 1, y: 0, scale: 1 }}
            transition={{ duration: 0.45, ease: "easeOut", delay: 0.4 }}
            viewport={{ once: true, amount: 0.25 }}
            whileHover={{
              y: -10,
              rotate: -0.6,
              transition: { type: "spring", stiffness: 300, damping: 10 },
            }}
          >
            <Link to="/menu/cakes" onClick={() => handleCategoryCakes(productsCategory.cakes)}>
              <WrapInfo>
                <MenuName></MenuName>
                <MenuBtn></MenuBtn>
              </WrapInfo>
              <MenuCardImage src={CakeImg} alt="" />
            </Link>
          </MenuCard>
          <MenuCard
            initial={{ opacity: 0, y: 18, scale: 0.98 }}
            whileInView={{ opacity: 1, y: 0, scale: 1 }}
            transition={{ duration: 0.45, ease: "easeOut", delay: 0.4 }}
            viewport={{ once: true, amount: 0.25 }}
            whileHover={{
              y: -10,
              rotate: -0.6,
              transition: { type: "spring", stiffness: 300, damping: 10 },
            }}
          >
            <Link to="/menu/cupcakes" onClick={() => handleCategoryCakes(productsCategory.cupcakes)}>
              <WrapInfo>
                <MenuName></MenuName>
                <MenuBtn></MenuBtn>
              </WrapInfo>
              <MenuCardImage src={CapCakeImg} alt="" />
            </Link>
          </MenuCard>
          <MenuCard
            initial={{ opacity: 0, y: 18, scale: 0.98 }}
            whileInView={{ opacity: 1, y: 0, scale: 1 }}
            transition={{ duration: 0.45, ease: "easeOut", delay: 0.4 }}
            viewport={{ once: true, amount: 0.25 }}
            whileHover={{
              y: -10,
              rotate: -0.6,
              transition: { type: "spring", stiffness: 300, damping: 10 },
            }}
          >
            <Link to="/menu/macarons" onClick={() => handleCategoryCakes(productsCategory.macarons)}>
              <WrapInfo>
                <MenuName></MenuName>
                <MenuBtn></MenuBtn>
              </WrapInfo>
              <MenuCardImage src={MakaronImg} alt="" />
            </Link>
          </MenuCard>
          <MenuCard
            initial={{ opacity: 0, y: 18, scale: 0.98 }}
            whileInView={{ opacity: 1, y: 0, scale: 1 }}
            transition={{ duration: 0.45, ease: "easeOut", delay: 0.4 }}
            viewport={{ once: true, amount: 0.25 }}
            whileHover={{
              y: -10,
              rotate: -0.6,
              transition: { type: "spring", stiffness: 300, damping: 10 },
            }}
          >
            <Link to="/menu/tarts" onClick={() => handleCategoryCakes(productsCategory.tarts)}>
              <WrapInfo>
                <MenuName></MenuName>
                <MenuBtn></MenuBtn>
              </WrapInfo>
              <MenuCardImage src={TartImg} alt="" />
            </Link>
          </MenuCard>
        </MenuList>
      </Container>
    </MenuSection>
  );
};
