import {
  MobMenuContainer,
  MobMenuSection,
  MobMenuBtn,
  MobMenuList,
  NavLink,
  BtnHeaderClouse,
  Backdrop
} from "./MobMenu.styled";

export const MobMenu = ({ clouseMobMenu, open }) => {
  const handleClouse = () => {
    clouseMobMenu();
  };
  return (
    <Backdrop open={open} onClick={handleClouse}>
    <MobMenuSection open={open}>
      <MobMenuContainer>
        <div>
          <BtnHeaderClouse type="button" onClick={handleClouse}>
            x
          </BtnHeaderClouse>

          <nav>
            <MobMenuList>
              <li>
                <NavLink to="/" onClick={handleClouse}>
                  
                </NavLink>
              </li>
              <li>
                <NavLink to="/menu" onClick={handleClouse}>
                  
                </NavLink>
              </li>
              <li>
                <NavLink to="/about" onClick={handleClouse}>
                   
                </NavLink>
              </li>
            </MobMenuList>
          </nav>
        </div>
        <MobMenuBtn to="/menu"> </MobMenuBtn>
      </MobMenuContainer>
    </MobMenuSection>
    </Backdrop>
  );
};
