import { useDispatch, useSelector } from "react-redux";
import { Container, OrderSection, FormTitle,Form,Input, PageTitle, SubmitButton,WrapInfoOrder, SummaryBox, TitleListOrder, ListOrder, SummaryText, CartItem, ItemImage, ItemInfo, ItemName, ItemPrice, QuantityControl, QuantityBtn } from "./OrderPage.styled"
import { getCartItems } from "../../redux/selectors";
import { decrementQuantity, incrementQuantity } from "../../redux/cartSlice";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

export const OrderPage = () => {
    const cart = useSelector(getCartItems);
     const dispatch = useDispatch();
    const navigate = useNavigate();
      const handleIncrementQuantity = (item) => dispatch(incrementQuantity(item));
      const handleDecrementQuantity = (item) => dispatch(decrementQuantity(item));
      useEffect(()=>{
        if (cart.length === 0) {
          navigate('/menu/allProducts', { replace: true });
        }
      },[cart,navigate])
    return(
        <main>
            <OrderSection>
                <Container>
                    <PageTitle> </PageTitle>
                    <WrapInfoOrder>
                        <Form>
                            <FormTitle>  </FormTitle>
                            <label>
                                '   
                                <Input type="text"/>
                            </label>
                             <label>
                                
                                <Input type="number"/>
                            </label>
                             <label>
                                 
                                <Input type="email"/>
                            </label>
                             <label>
                                
                                <Input type="text"/>
                            </label>
                             <label>
                                 
                                <Input type="text"/>
                            </label>
                            <SubmitButton> </SubmitButton>
                        </Form>
                        <SummaryBox>
                            <TitleListOrder> </TitleListOrder>
                            <ListOrder>
                               {cart.map((item) => (
                                          <CartItem key={item.id}>
                                            <ItemImage src={item.image} />
                                            <ItemInfo>
                                              <ItemName to={`/menu/allProducts/${item.id}`}>{item.title}</ItemName>
                                              <ItemPrice>{item.price} </ItemPrice>
                                            </ItemInfo>
                                            <QuantityControl>
                                              <QuantityBtn
                                                type="button"
                                                onClick={() => handleDecrementQuantity(item)}
                                              >
                                                -
                                              </QuantityBtn>
                                              <p>{item.quantity}</p>
                                              <QuantityBtn
                                                type="button"
                                                onClick={() => handleIncrementQuantity(item)}
                                              >
                                                +
                                              </QuantityBtn>
                                            </QuantityControl>
                                          </CartItem>
                                        ))}
                            </ListOrder>
                            <SummaryText>: {cart.reduce((total, item) => total + item.price * item.quantity, 0)} </SummaryText>
                        </SummaryBox>
                    </WrapInfoOrder>
                </Container>
            </OrderSection>
        </main>
    )
}